import tuiIcon from "@/components/icon/icon"
import tuiUpload from '@/components/tui-upload/tui-upload.vue'
export default {
	components: {
		tuiIcon,
		tuiUpload
	},
	data() {
		return {
			ordersn: '', //订单编号
			id: '', //商品ID
			goods: {},
			typeClass:'',
			chooseType:'请选择',
			reason:'请选择',
			txt:'',
			typeShow:true,
			items1: [{
					value: '0',
					name: '未收到货'
				},
				{
					value: '1',
					name: '已收到货'
				},
			],
			items2: [],
			current1: '',
			current2: '',
			imageData: [],
			money:'',
			//上传地址
			serverUrl:"http://dh.xmvogue.com/index.php/API/Service/getimage"
		};
	},
	onLoad() {
		this.goods=uni.getStorageSync('refund');
		console.log(this.goods);
		this.money=this.goods.subtotal;
		this.getReason()
	},
	methods: {
		// 获取退货原因
		getReason(){
			this.$xm.post('/Service/reasonlist','',(res)=>{
				console.log(res);
				this.items2=res.data;
			})
		},
		// 退款金额
		replaceInput(e){
			if(e.target.value>this.goods.subtotal){
				this.money=this.goods.subtotal;				
			}
		},
		togoods() {
			uni.navigateTo({
				url:'../../../goods/goods?gid='+e
			})
		},
		//货物状态弹窗
		showtype() {
			this.typeClass = 'show';
			this.typeShow=true;
		},
		//关闭货物状态弹窗
		hideType() {
			this.typeClass = 'hide';
			setTimeout(() => {
				this.typeClass = 'none';
			}, 200);
		},
		// 退款原因弹窗
		showReason(){
			this.typeClass = 'show';
			this.typeShow=false;
		},
		radioChange(e) {				
			for (let i = 0; i < this.items1.length; i++) {
				if (this.items1[i].value === e.target.value) {
					this.current1 = i;
					this.chooseType=this.items1[i].name;
					break;
				}
			}
		},
		reasonChange(e) {
			this.current2=e.target.value;
			this.reason=this.items2[e.target.value-1].reason;
			
		},
		// 上传图片
		result(e) {
			console.log(e)
			this.imageData = e.imgArr;
		},
		//移除图片
		remove(e) {				
			console.log(e)
			let index = e.index
		},
		// 提交退款
		submitData(){
			if(this.chooseType=='请选择'){
				uni.showToast({
					title:'货物状态不能为空',
					icon:'none'
				})
			} else if(this.reason=='请选择'){
				uni.showToast({
					title:'退款原因不能为空',
					icon:'none'
				})
			}else{
				let data={
					order_sn:this.goods.order_sn,
					proid:this.goods.proid,
					status:this.chooseType,
					reason:this.reason,
					money:this.money,
					txt:this.txt,
					img:this.imageData
				};
				console.log(data);
			}
			
		}
	}
}
